<template>
  <div>
    <div class="titleBar">
      <div
        class="float-left cursor-pointer"
        @click="logout"
      >
        <i class="fal fa-sign-out-alt" /> Logout
      </div>
      <div class="float-right">
        <div class="title">
          {{ user.username }}
        </div>
        <img :src="user.profilePic || require('assets/avatar-default.jpg')">
      </div>
    </div>
    <div class="content">
      <div class="header">
        Friends <span v-if="user.friends && user.friends.length"> - [{{ user.friends.length }}]</span>
        <div @click="reloadFriends">
          <i class="fas fa-sync-alt float-right clickable" />
        </div>
      </div>
      <template v-if="user.friends">
        <em v-if="!user.friends.length">No Friends online</em>
        <div
          v-for="friend in user.friends"
          v-else
          :key="friend.uid"
          class="friend"
        >
          <img
            :src="friend.avatar"
            class="clickable round-highlight"
          >
          <div class="name">
            {{ friend.username }}
          </div>
        </div>
      </template>
      <div class="header">
        Settings
      </div>
      <em>Settings currently not available</em>
    </div>
    <div class="footer">
      Core version: <em>{{ coreVersion }}</em>
      <br>
      Launcher version: <em>{{ launcherUIVersion }}</em>
    </div>
  </div>
</template>
<script>
import { mapGetters } from 'vuex'
// eslint-disable-next-line no-unused-vars
export default {
  computed: {
    ...mapGetters({
      user: 'authentication/user'
    }),
    showPeek () {
      return this.$store.getters['ui/showPeek']
    },
    coreVersion () {
      // eslint-disable-next-line no-undef
      return launcherVersion()
    },
    launcherUIVersion () {
      return process.env.VUE_APP_LAUNCHER_VERSION || 'Unknown'
    }
  },
  created () {
    if (!this.user.friends.length) this.reloadFriends()
  },
  methods: {
    checkClose (i) {
      if (i.target === this.$el) this.closePeek()
    },
    closePeek () {
      this.$store.dispatch('ui/setPeek', false)
    },
    logout () {
      this.closePeek()
      this.$store.dispatch('authentication/logout')
    },
    reloadFriends () {
      this.$store.dispatch('authentication/loadFriends')
    }
  }
}
</script>
<style lang="stylus" scoped>
.friend {
  display: inline-block;
  position: relative;

  img {
    width: 48px;
    border-radius: 100%;
    margin: 8px;
    margin-left: 13px;
    margin-right: 13px;
    background-color: alpha(black, 0.05);
  }

  &:hover {
    cursor: pointer;

    .name {
      display: block;
    }
  }

  .name {
    text-align: center;
    position: absolute;
    bottom: -10px;
    width: 100%;
    display: none;
    white-space: nowrap;
  }
}

.content {
  padding: 16px;
}
</style>
